import React from 'react'
import "./index.css";
import { NavLink } from "react-router-dom";
const Navbar = () => {
    return (
        <>
            <div className="container-fluid nav_bg">
                <div className="row">
                    <div className="col-10 mx-auto">
                        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                            <NavLink className="navbar-brand" to="/home">ðŸ¥˜Foods.</NavLink>
                            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon icon"></span>
                            </button>

                            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                                <ul className="navbar-nav ml-auto">
                                    <li className="nav-item ">
                                        <NavLink activeClassName="menu_active" area-current="page" className="nav-link" to="/home">Home </NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink activeClassName="menu_active" className="nav-link" to="/service">Recipe</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink activeClassName="menu_active" className="nav-link" to="/about">About</NavLink>
                                    </li>


                                </ul>

                            </div>
                        </nav>

                    </div>
                </div>
            </div>
        </>
    )
}
export default Navbar;